class GlobalModule {
    constructor() {
        this.modules = new Map();
        this.localization = null;
        this.cloneUtil = null;
        this.encyrpctionUtil = null;
        this.performanceUtil = null;
        this.dateUtil = null;
        this.observer = null;
        this.sharedHeaders = {};
    }
    setLocalization(localization) {
        this.localization = localization;
        return this;
    }
    getLocalization() {
        return this.localization;
    }
    registerModule(module) {
        this.modules.set(module.constructor.name, module);
        return this;
    }
    getModule(constructor) {
        return this.modules.get(constructor.name);
    }
    setCloneUtil(util) {
        this.cloneUtil = util;
        return this;
    }
    getCloneUtil() {
        return this.cloneUtil;
    }
    setEncryptionUtil(util) {
        this.encyrpctionUtil = util;
        return this;
    }
    getEncryptionUtil() {
        return this.encyrpctionUtil;
    }
    setPerformanceUtil(util) {
        this.performanceUtil = util;
        return this;
    }
    getPerformanceUtil() {
        return this.performanceUtil;
    }
    setDateUtil(util) {
        this.dateUtil = util;
        return this;
    }
    getDateUtil() {
        return this.dateUtil;
    }
    setObserver(observer) {
        this.observer = observer;
        return this;
    }
    createObserver() {
        if (!this.observer)
            return;
        return new this.observer();
    }
    addToSharedHeaders(header) {
        this.sharedHeaders = { ...this.sharedHeaders, ...header };
    }
    getSharedHeaders() {
        return this.sharedHeaders;
    }
    removeSharedHeaders(...keys) {
        keys.forEach((key) => delete this.sharedHeaders[key]);
    }
    clear() {
        this.localization = null;
        this.cloneUtil = null;
        this.encyrpctionUtil = null;
        this.performanceUtil = null;
        this.dateUtil = null;
        this.observer = null;
        this.sharedHeaders = {};
        this.modules.clear();
    }
}
const createGlobalModule = () => {
    let globalModule = window.$globalModule;
    if (!globalModule)
        globalModule = window.$globalModule = new GlobalModule();
    return globalModule;
};
export const globalModule = createGlobalModule();
