export class UniqueList extends Array {
    constructor(compareFn, value) {
        super();
        this.compareFn = compareFn;
        if (value)
            this.push(...value);
    }
    push(...items) {
        const filteredItems = items.filter((e) => this.every((i) => !this.compareFn(e, i)));
        return super.push(...filteredItems);
    }
    remove(item) {
        const index = this.findIndex((e) => this.compareFn(e, item));
        if (index > -1)
            this.splice(index, 1);
        return index;
    }
    compare(a, b) {
        return this.compareFn(a, b);
    }
}
export const createUniqueList = (compareFn, value) => new UniqueList(compareFn, value);
