import { globalModule } from "../global-module/global-module";
const cloneDecorator = (deep, target, propertyKey, descriptor) => {
    const originalMethod = descriptor.value;
    descriptor.value = function (...args) {
        const cloneUtil = globalModule.getCloneUtil();
        const newArgs = cloneUtil
            ? args.map((arg) => deep ? cloneUtil.cloneDeep(arg) : cloneUtil.clone(arg))
            : args;
        return originalMethod.apply(this, newArgs);
    };
    return descriptor;
};
export const cloneArgs = (target, propertyKey, descriptor) => {
    return cloneDecorator(false, target, propertyKey, descriptor);
};
export const cloneDeepArgs = (target, propertyKey, descriptor) => {
    return cloneDecorator(true, target, propertyKey, descriptor);
};
