class DefaultEncryptUtil {
    encrypt(value) {
        return value
            .split("")
            .map((e) => String.fromCharCode(e.charCodeAt(0) + 1))
            .join("");
    }
    decrypt(value) {
        return value
            .split("")
            .map((e) => String.fromCharCode(e.charCodeAt(0) - 1))
            .join("");
    }
}
export const defaultEncryptUtil = new DefaultEncryptUtil();
