import cloneDeep from "lodash/cloneDeep";
class DefaultCloneUtil {
    clone(item) {
        if (item instanceof Array)
            return [...item];
        else if (typeof item === "object")
            return { ...item };
        return item;
    }
    cloneDeep(item) {
        return cloneDeep(item);
    }
}
export const defaultCloneUtil = new DefaultCloneUtil();
